<template>
  <div class="personal-content-body">
    <div class="table-content">
      <div class="clear table-content-title">
        <div class="left">基本信息</div>
        <el-input placeholder="搜索" v-model="searchText" class="header-right-time right" :clearable='true' @change="initData()">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
        <!-- <el-date-picker v-model="selectTime" type="date" placeholder="选择日期" :clearable='true' value-format="yyyy-MM-dd" @change="getListData(1)" class="header-right-time right">
            </el-date-picker> -->
      </div>
      <div class="table-group">
        <div class="table-group-content">
          <div class="clear table-group-title">
            <div class="left breadcrumb">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item>基本信息（{{total}}）</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="goCreatnew">
                <span class="iconfont icon-add"></span>添加新投资人
              </div>
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="toDownloadCusInfo">
                <span class="iconfont icon-daoru"></span>导出投资人信息
              </div>
            </div>
            <div class="right">
              <label for="upload-fund-input">
                <div class="table-group-title-btn btn-blue">
                  <span class="iconfont icon-daochu2"></span>导入投资人信息
                </div>
              </label>
              <input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadFundExcel($event)" />
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="toDownloadCusExcel">
                <span class="iconfont icon-daoru"></span>下载投资人模版
              </div>
            </div>
          </div>
          <div class="table-group-tab">
            <!-- <div class="table-group-tab-item">个人信息</div>
                <div class="table-group-tab-item current">密码设置</div> -->
            <div class="clear"></div>
            <div class="line"></div>
          </div>
          <div class="historyapply-group-tablebox table-group-tablebox">
            <div class="table-normal-box">
              <table class="table-normal">
                <tr>
                  <!-- <th>
                        <el-checkbox v-model="selectAll" @change="selectallclick($event)"></el-checkbox>
                      </th> -->
                  <th>序号</th>
                  <th>投资人</th>
                  <th>手机号</th>
                  <th>邮箱</th>
                  <th>客户端账号</th>
                  <th>操作</th>
                </tr>
                <tr v-for="(item,index) in datalist" :key="index">
                  <!-- <td>
                        <el-checkbox :disabled="item.user_id!=null" v-model="item.checked" @change="selectoneclick($event)"></el-checkbox>
                      </td> -->
                  <td>{{pageSize*(currentPage-1)+index+1}}</td>
                  <td>{{item.name}}</td>
                  <td v-if="item.cell==''">无</td>
                  <td v-else>{{item.cell}}</td>
                  <td v-if="item.email==''">无</td>
                  <td v-else>{{item.email}}</td>
                  <td>
                    <span v-if="item.status==1">已开启</span>
                    <span v-else>未开启</span>
                  </td>
                  <td>
                    <span class="table-link" @click="goEditorInfo(item.id)">编辑</span>
                    <span class="table-link" @click="deleteInfo(item.id)">删除</span>
                  </td>
                </tr>
              </table>
              <div class="table-nodata" v-if="total == 0">
                <img src="../../images/table_nodata.png"> 没有对应数据
              </div>
            </div>
            <div class="pagination-normal" v-if="total!=0">
              <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <!--                         项目管理权限分配弹窗                  -->
      <!-- 文件上传弹窗 -->
      <!-- <el-dialog title="项目管理权限分配" :visible.sync="changePermissions" width="500px" center :append-to-body='true'>
            <div class="dialog_content">
              <div class="form-group">
                <div class="clear peopleSelectTitle">
                  <div class="left peopleSelectTitle-text">选择管理人</div>
                  <el-select class="right peopleSelectTitle-select" v-model="value7" filterable placeholder="请选择" @change="changemanage($event)">
                    <el-option-group v-for="group in peoplelist" :key="group.name" :label="group.name">
                      <el-option v-for="item in group.children" :key="item.id" :label="item.name" :value="item.id">
                      </el-option>
                    </el-option-group>
                  </el-select>
                </div>
              </div>
            </div>
            <div slot="footer" class="dialog_footer clear">
              <button class="dialog-btn-submit btn-blue" @click="submitClick()">提交</button>
            </div>
          </el-dialog> -->
    </div>
  </div>
</template>
<script>
import {
  cusList,
  deleteCus,
  downloadCusExcel,
  cusExcel,
  fileTemplate
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
import api from '../../config/url'; //引用url.js
export default {
  data() {
    return {
      currentPage: 1,
      total: null,
      pageSize: 10,
      searchText: '', //查询数据
      datalist: [], //项目列表
      // selectAll: false, //是否全选
      // changePermissions: false, //弹窗显示
      // peoplelist: [], //人员列表
      // value7: '', //已选中人员
      // checkedpersonid: -1,
      // submitType: -1, //修改方式（批量0，单个-1）
      // selectedProjectId: -1, //单个修改选中的项目id
      isNoInfoShow: false,
    }
  },
  created() {
  	if (this.$route.query.page) {
  		this.currentPage = parseInt(this.$route.query.page);
  	}
    this.initData();
    // this.getPeopleList();
  },
  computed: {
    // selectedProject: function() {
    //   let sp = [];
    //   this.datalist.forEach(item => {
    //     if (item.checked) {
    //       sp.push(item.company_id);
    //     }
    //   })
    //   return sp;
    // }
  },
  mounted() {},
  methods: {
    initData() {
      this.datalist = [];
      // this.selectAll = false;
      //this.searchText = '';
      cusList({
        number: this.pageSize,
        page: this.currentPage,
        search: this.searchText
      }).then(res => {
        //console.log(res);
        if (res.data.msgNo == 1) {
          this.datalist = res.data.payload.data;
          this.total = res.data.payload.total;
          if (res.data.payload.data.length == 0) {
          	this.isNoInfoShow = true;
          } else {
          	this.isNoInfoShow = false;
          }
        }
      })
    },
    currentChange(val) {
      if (this.total > this.pageSize) {
        this.currentPage = val;
        this.initData();
      }
    },

    //删除一行
    deleteInfo(id) {
      dialogConfirmHasbtn('确定要删除该投资人信息？').then(() => {
        this.addaccountDialogVisible = false;
        deleteCus({
          id: id,
        }).then(res => {
          //console.log(res);
          if (res.data.msgNo == 1) {
            //console.log(res.data.payload);
            dialogConfirm('success', '删除成功')
            if(this.datalist.length == 1 && this.currentPage > 1){
            		this.currentPage = this.currentPage - 1;
            }
            this.initData();
          } else {
            dialogConfirm('fail', res.data.message);
          }
        })
      }).catch(() => {});
    },
    //去修改
    goEditorInfo(id) {
      this.$router.push({
        path: '/home/investorsInfo',
        query: {
          id: id,
          page: this.currentPage
        }
      })
    },
    //去新建一行
    goCreatnew() {
      this.$router.push({
        path: '/home/investorsInfo',
      })
    },
    //下载模板
    toDownloadCusExcel() {
      fileTemplate({
        fileName:"tzrxx"
      }).then(res => {
        console.log(res);
        if (res.data.msgNo == 1) {
          //  console.log(res.data.payload);
          window.location.href = res.data.payload;
        } else {
          dialogConfirm('info', res.data.message)
        }
      });
    },

    // 导出投资人信息
    toDownloadCusInfo() {
      // exportCusExcel().then(res => {
      //   console.log(res);
      //   // if (res.data.msgNo == 1) {
      //   //   //  console.log(res.data.payload);
      //   //   window.location.href = res.data.payload;
      //   // } else {
      //   //   dialogConfirm('info', res.data.message)
      //   // }
      // });
      // window.location.href = api.url + '/web/Customer/exportCusExcel';
      let table_token =   window.localStorage.getItem("htcx_server_table_token");
      window.location.href = api.url + '/web/Customer/exportCusExcel?table_token='+table_token;
    },
    //导入Excel文件
    uploadFundExcel(e) {
      let inputDOM = this.$refs.inputer;
      // 通过DOM取文件数据
      let file = inputDOM.files[0];
      if (file == undefined) {
        return false;
      }
      console.log(file);
      console.log(file.name);
      if (!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
        dialogConfirm('info', '请选择Excel格式文件')
        inputDOM.value = '';
        return false
      }
      cusExcel({}, file).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '导入成功');
          this.currentPage = 1;
          this.initData();
        } else {
          dialogConfirm('info', res.data.message);
        }
      })
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.table-group-title-btn {
  &>span {
    display: inline-block;
    width: 20px;
    height: 20px;
  }
  .icon_upload {
    background: url('../../images/icon_upload.png') no-repeat;
    background-size: 90%;
  }
  .icon_download {
    background: url('../../images/icon_download.png') no-repeat;
    background-size: 100%;
    margin-top: 4px;
  }
}

.peopleSelectTitle-text {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 40px;
  margin-right: 10px;
}

.peopleSelectTitle {
  border-bottom: 1px solid #dcdfe6;
} // .table-group-title-btn {
//         line-height: 30px;
//         font-size: 14px;
//         padding: 0px 10px;
//         margin-right: 0px;
//         margin-left: 10px;
//         position: relative;
//         top: -15px;
//         &>span {
//             display: inline-block;
//             width: 20px;
//             height: 20px;
//             float: left;
//             margin-top: 5px;
//             margin-right: 5px;
//         }
//     }
.icon_upload {
  background: url('../../images/icon_upload.png') no-repeat;
  background-size: 100%;
}

.icon_download {
  background: url('../../images/icon_download.png') no-repeat;
  background-size: 100%;
  margin-top: 4px;
}

</style>



